.maindisplay {
  height: 100%;
  width: 100%;
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.welcomemsg {
  height: 100%;
  padding: 0 2rem;
  line-height: 1.4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.welcomemsg h2 {
  margin-bottom: 1rem;
}


.title {
  font-size: 1.7rem;
  text-align: center;
  padding: 10px;
}

.text {
  font-size: 1.2rem;
  text-align: center;
  padding: 10px;
}

.picture {
  width: 100%;
  height: 100%;
  display: flex;
  align-content: center;
  justify-content: center;
}


.picture>svg {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;

  max-width: 50vw;
  max-height: 50vh;
}

.loading {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-content: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 4s linear infinite;
  -moz-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}